
import Api from '@/js/common'
import Search from 'vux/src/components/search/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import { mapGetters, mapMutations } from 'vuex'
import noData from '@/components/noData'

export default {
components: {
ViewBox,
Search,
Badge,
noData
},
name: 'app',
data() {
return {
isNoData: false,
results: [],
value: '',
serachList: [],
page: 1,
data: []
}
},
computed: {
...mapGetters(['device'])
},
methods: {
...mapMutations('submit', ['newAnswer']),
setFocus() {
this.$refs.search.setFocus()
},
onSubmit() {
this.$refs.search.setBlur()
this.data = []
this.page = 1
this.isNoData = false
this.scrollTop()
this.getList()
},
onFocus() {
console.log('on focus')
},
onCancel() {
this.$router.go(-1)
},
getList() {
var vmSelf = this
if (vmSelf.value == '') {
vmSelf.alertInfo('请输入搜索内容')
return
}
vmSelf.$vux.loading.show({
text: 'Loading'
})
var submitObj = {
sear_val: vmSelf.value,
page: vmSelf.page
}
Api.quesSearch(submitObj)
.then(function(response) {
vmSelf.$vux.loading.hide()
vmSelf.serachList = response
if (vmSelf.page === 1) {
vmSelf.$refs.myscroller.finishPullToRefresh()
vmSelf.data = []
}
let start = vmSelf.serachList.length
for (let i = 0; i < start; i++) {
vmSelf.data.push(vmSelf.serachList[i])
}
if (vmSelf.data.length === 0) {
vmSelf.isNoData = true
} else {
vmSelf.isNoData = false
}
if (start) {
vmSelf.page++
vmSelf.$refs.myscroller.finishInfinite(0)
} else {
return vmSelf.$refs.myscroller.finishInfinite(2)
}
})
.catch(function(err) {
console.log('=====', err)
})
},
loadMore() {
if (this.page == 1) {
return this.$refs.myscroller.finishInfinite(2)
}
this.getList()
},
refresh() {
this.data.length = 0
this.page = 1
this.getList()
this.$refs.myscroller.finishPullToRefresh()
},
scrollTop() {
this.$refs.myscroller.scrollBy(0, 0, false)
},
goto(id,random,status) {
this.newAnswer("del")
if(status == 0){//收到的问卷
this.$router.push({
path: `/finishDo/${id}/${random}`
})
}else{//创建的问卷
this.$router.push({
path: `/toDo/${id}/${random}`
})
}

},
alertInfo(title) {
var vmSelf = this
this.$vux.alert.show({
title: '提示',
content: title,
onShow() {
console.log("Plugin: I'm showing")
},
onHide() {
console.log("Plugin: I'm hiding now")
}
})
}
},
beforeRouteEnter(to, from, next) {
console.log("3333333333333333333333",from.name)
if (
from.name !== 'toDo' &&
from.name !== 'finishDo'
) {
next(vm => {

vm.data = []
vm.page = 1
vm.serachList = []
vm.results = []
vm.value = ''





vm.isNoData = false
let that = vm
setTimeout(function() {
that.setFocus()
}, 100)
})
} else {
next()
}
},
mounted() {
this.setFocus()
}
}



